:root {
  color: rgb(233, 233, 233);
}

* {
  font-family: Lucida Console, monospace;
}

.App {
  background-color: rgb(26, 26, 26);
  height: 100vh;
  width: 100vw;
  position: relative;
  overflow: hidden;
}

.header {
  position: fixed;
  /* height: 50px; */
  width: 100%;
  z-index: 105;
}

.body {
  position: relative;
  height: 100%; 
}

.footer {
  position: fixed;
  height: 30px;
  width: 100%;
  padding: 25px 0 25px 0;
  bottom: 0;
  z-index: 105;
  background-color: rgb(26, 26, 26);
}